import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import ReactPlayer from 'react-player/lazy';
import './ChannelStreaming.css';
import logo from '../../Assets/images/iptvlogo.png';
import { Focusable, FocusableSection } from 'react-js-spatial-navigation';
import '../../Components/layout/Navbar/Navbar.css';
import { useNavigate } from 'react-router-dom';


const ChannelStreaming = () => {

  const [searchParams, setSearchParams] = useSearchParams();
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const backEvent = function (e) {
      if (e.keyName === 'back') {
        console.log('back event', e, window.location.href);
        window.history.back();
      }
    };

    // add eventListener for tizenhwkey (Back Button)
    window.addEventListener('tizenhwkey', backEvent);
    return () => {
      window.removeEventListener('tizenhwkey', backEvent);
    };
  }, []);

  return (
    <div className='channelStreamingWrapper'>
     <nav className='navbarWrapper'>
      <span className='logoWrapper'>
        <img className='logoImage' src={logo} alt='Logo' />
      </span>
      <div className='linksAndSearchWrapper'>
        <div className='searchWrapper'></div>
        <div className='linksWrapper'>
          <FocusableSection defaultElement='.tvChannels'>
            <Focusable>
              <div
                 onClick={() => navigate(-1)}
                className='linkItem tvChannels'
              >
                TV Channels
              </div>
            </Focusable>
          
          </FocusableSection>
        </div>
      </div>
    </nav>

      <ReactPlayer
        className='player-wrapper'
        url={
          window.decodeURIComponent(searchParams.get('channelUrl'))
        }
        controls={true}
       
        autoPlay
        playing        
        width='98%'
        height='90%'
        onBuffer={() => {
          console.log('buffer');
          setLoading(true);
        }}
        onBufferEnd={() => {
        //  console.log('bufferEnd');
          loading && setLoading(false);
        }}
        onPlay={() => {
         // console.log('play');
          loading && setLoading(false);
        }}
      />
      {loading && (
        <div className='loaderContainer'>
          <div className='fa-3x'>
            <i className='fas fa-spinner fa-pulse'></i>
          </div>
        </div>
      )}
    </div>
  );
};

export default ChannelStreaming;
